import React, {useEffect} from 'react'
import * as echarts from 'echarts/core'
import {PieChart, PieSeriesOption} from "echarts/charts";
import {
    TitleComponent,
    TitleComponentOption,
    TooltipComponent,
    TooltipComponentOption,
    LegendComponent,
    LegendComponentOption
} from "echarts/components";
import {CanvasRenderer} from "echarts/renderers";
import {downloadPng} from "../utils/download";
import {HiDownload} from "react-icons/hi";

interface PercentageDoughnutChartProps {
    chartId?: string;
    width?: string;
    height?: string;
    title?: string;
    percentage: number
}

type ECOption = echarts.ComposeOption<PieSeriesOption | TitleComponentOption | TooltipComponentOption | LegendComponentOption>;

echarts.use([PieChart, TitleComponent, TooltipComponent, LegendComponent, CanvasRenderer])

function PercentageDoughnutChart(props: PercentageDoughnutChartProps) {
    const {chartId = 'default', width = '100%', height = '400px', title = 'Title', percentage = 0} = props
    const option: ECOption = {
        title: {
            text: title,
            top: 'top',
            textStyle: {
                fontSize: 24,
            }
        },
        series: [
            {
                type: "pie",
                radius: ["55%", "90%"],

                top: '15%',
                label: {
                    show: false,
                    position: "center"
                },
                labelLine: {
                    show: false
                },
                data: [
                    {name: 'Done', value: percentage},
                    {name: 'None', value: 100 - percentage},
                ]
            }
        ]
    }

    useEffect(() => {
        const chartContainerEl = document.getElementById(`chart-container-${chartId}`)!
        console.log(chartContainerEl);
        const chart = echarts.init(chartContainerEl, 'customed')
        chart.setOption(option)
    })

    const onDownload = async () => {
        await downloadPng(`chart-shooting-container`, title)
    }

    return (
        <div className="relative">
            <HiDownload className="absolute right-0 top-0 text-3xl cursor-pointer" onClick={onDownload}/>
            <div id="chart-shooting-container">
                <div id={`chart-container-${chartId}`} style={{width, height}}/>
                <p className="absolute left-1/2 transform -translate-x-1/2 -translate-y-1/2 text-primary text-3xl"
                   style={{top: '58%'}}
                >{percentage}%</p>
            </div>
        </div>
    )
}

export default PercentageDoughnutChart
